html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--background);
  color: var(--text-color);
}

.sub-header {
  color: var(--sub-header);
}

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: auto;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 40em) {
  h1 {
    font-size: 1.75rem;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(329, 70%, 58%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(37, 97%, 70%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-background-start: hsl(210, 78%, 56%);
  --toggle-background-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card-bg: hsl(228, 28%, 20%);
  --dark-card-bg-hover: hsl(228, 27%, 36%);
  --dark-text-1: hsl(228, 34%, 66%);
  --dark-text-2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card-bg: hsl(227, 47%, 96%);
  --light-text-1: hsl(228, 12%, 44%);
  --light-text-2: hsl(230, 17%, 14%);
  --background: var(--light-bg);
  --text-color:var(--light-text-2);
  --caption-text: var(--light-text-1);
  --card-background: var(--light-card-bg);
  --count-color: var(--light-text-2);
  --small-count: var(--light-text-2);
  --small-title: var(--light-text-1);
  --username-color: var(--light-text-1);
  --sub-header: var(--light-text-1);
  --toggle-background: var(--light-text-1);
  --button-color: var(--light-bg);
  --toggle-text: var(--light-text-1);
}

@media (prefers-color-scheme: dark) {
  :root {
    --sub-header: var(--dark-text-2);
    --background: var(--dark-bg);
    --text-color: var(--dark-text-2);
    --caption-text: var(--dark-text-1);
    --card-background: var(--dark-card-bg);
    --count-color: var(--dark-text-2);
    --small-count: var(--dark-text-2);
    --small-title: var(--dark-text-1);
    --username-color: var(--dark-text-1);
    --button-color: var(--dark-bg);
    --toggle-background: linear-gradient(225deg, var(--toggle-background-end) 0%, var(--toggle-background-start) 98.02%);
    --toggle-text: var(--dark-text-1);
  }
}
body.light {
  --background: var(--light-bg);
  --text-color:var(--light-text-2);
  --caption-text: var(--light-text-1);
  --card-background: var(--light-card-bg);
  --count-color: var(--light-text-2);
  --small-count: var(--light-text-2);
  --small-title: var(--light-text-1);
  --username-color: var(--light-text-1);
  --sub-header: var(--light-text-1);
  --toggle-background: var(--light-text-1);
  --button-color: var(--light-bg);
  --toggle-text: var(--light-text-1);
}

body.dark {
  --sub-header: var(--dark-text-2);
  --background: var(--dark-bg);
  --text-color: var(--dark-text-2);
  --caption-text: var(--dark-text-1);
  --card-background: var(--dark-card-bg);
  --count-color: var(--dark-text-2);
  --small-count: var(--dark-text-2);
  --small-title: var(--dark-text-1);
  --username-color: var(--dark-text-1);
  --button-color: var(--dark-bg);
  --toggle-background: linear-gradient(225deg, var(--toggle-background-end) 0%, var(--toggle-background-start) 98.02%);
  --toggle-text: var(--dark-text-1);
}

.container {
  max-width: 69.375rem;
  padding: 0 1.5625rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.card-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .card-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .card-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.875rem;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  margin-top: 1.5625rem;
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__toggle {
  margin-top: 1.5625rem;
}
.header__caption {
  color: var(--caption-text);
  font-weight: bold;
  font-size: 0.875rem;
}

.toggle {
  border: none;
  display: grid;
  grid-template-columns: 1fr 3.125rem;
  grid-template-rows: 1fr 1fr;
  gap: 0.125rem 0.5rem;
}
.toggle label {
  font-size: 0.875rem;
  align-self: center;
  color: var(--toggle-text);
  font-weight: bold;
}
.toggle label[for=dark] {
  grid-column: 1/2;
  text-align: right;
}
.toggle label[for=light] {
  grid-column: 2/3;
  text-align: left;
}
.toggle__wrapper {
  height: 1.5rem;
  width: 3rem;
  grid-column: 2/3;
  grid-row: 1/2;
  position: relative;
}
.toggle input[type=radio] {
  opacity: 0;
  cursor: pointer;
  height: 100%;
  width: 40%;
  z-index: 1;
  display: inline-block;
  position: relative;
  margin: 0;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid white;
}
.toggle__background {
  display: block;
  position: absolute;
  pointer-events: none;
  height: 100%;
  width: 3rem;
  top: 0;
  border-radius: 0.75rem;
  height: 100%;
  z-index: 0;
  background: var(--toggle-background);
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--button-color);
  transition: all 150ms ease-in-out;
  z-index: 2;
}
.toggle #light:checked ~ .toggle__button {
  right: 0.1875rem;
  left: calc(100% - 1.3125rem);
}

.toggle__button:hover {
  cursor: pointer;
}

.toggle__label:hover {
  cursor: pointer;
}

.card {
  background-color: var(--card-background);
  color: var(--dark-text-2);
  flex: 1;
  width: 100%;
  text-align: center;
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  overflow: hidden;
  position: relative;
}
.card--title {
  font-size: 0.875rem;
  color: var(--small-title);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
}
.card--change {
  display: flex;
  justify-content: space-between;
}
.card--change--number {
  font-size: 1.875rem;
  font-weight: bold;
  color: var(--small-count);
}
.card--small {
  display: flex;
  flex-direction: column;
  gap: 1.5625rem;
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 0.3125rem;
  background: linear-gradient(225deg, var(--instagram-start), var(--instagram-middle), var(--instagram-end));
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.75rem;
}
.card__icon {
  margin-right: 0.5rem;
  height: 1.25rem;
  width: 1.25rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: bold;
  color: var(--username-color);
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--count-color);
  margin-bottom: 0.25rem;
  line-height: 1;
  letter-spacing: -0.125rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__label {
  color: var(--dark-text-1);
  font-size: 0.75rem;
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 0.3125rem;
}
.card__change {
  font-size: 0.75rem;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
}
.card__change img {
  margin-right: 0.25rem;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}

.card:hover {
  opacity: 0.8;
  transition: ease-in 150ms;
  cursor: pointer;
}
.card:hover .card__count--big {
  font-size: 3.75rem;
  transition: ease-in 150ms;
}
.card:hover .card--change--number {
  font-size: 2rem;
  transition: ease-in 150ms;
}